import Gallery from 'react-photo-gallery';
import { photos } from './source';
import Carousel, { Modal, ModalGateway } from 'react-images';
import { useCallback, useState } from 'react';
import './style.scss';

const photoSource = photos.map(e => ({
  ...e,
  src: e.src.indexOf('http') === -1 ?  process.env.PUBLIC_URL + '/photo/' + e.src : e.src,
}))

export const HomePage = () => {
  const [currentImage, setCurrentImage] = useState(0);
  const [viewerIsOpen, setViewerIsOpen] = useState(false);

  const openLightbox = useCallback((event, { photo, index }) => {
    setCurrentImage(index);
    setViewerIsOpen(true);
  }, []);

  const closeLightbox = () => {
    setCurrentImage(0);
    setViewerIsOpen(false);
  };

  return (
    <div>
      <div className='gallery'>
        <Gallery photos={photoSource} onClick={openLightbox} direction='column' />
      </div>
      <ModalGateway>
        {viewerIsOpen ? (
          <Modal onClose={closeLightbox}>
            <Carousel
              currentIndex={currentImage}
              views={photoSource.map(x => ({
                ...x,
                source: x.src,
                // srcset: x.srcSet,
                // caption: x.title
              }))}
            />
          </Modal>
        ) : null}
      </ModalGateway>
    </div>
  );
}
